@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);
@import url(https://fonts.googleapis.com/css?family=Karla:400,700);
*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  margin: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

ul {
  list-style: none; }

p,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

div > p {
  margin-bottom: 0; }

table > .strings-list__heading, .strings-list__body, .sideBarUser__name, .user-menu__name {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500; }

div .search-form__button, a > .strings-list__button, div > .search-form__select, div > .search-form__input--text {
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  line-height: 15px;
  font-weight: 500; }

.search-form__text, .strings-list__elements {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500; }

div .search-form__button, a > .strings-list__button {
  border-radius: 8px;
  padding: 10px 0;
  width: 140px;
  margin-bottom: 30px; }

.layout {
  width: 100%;
  display: flex;
  min-height: 100vh;
  min-width: 32rem;
  background: #ffffff; }

.sidebar {
  display: inline-block;
  min-width: 203px;
  max-width: 20%;
  background-color: inherit;
  min-height: 100vh;
  padding-top: 30px; }

.main-content {
  display: inline-block;
  width: 80%;
  min-height: 100vh;
  background-color: #f5f5f5;
  padding-top: 30px; }

.header {
  padding: 0 26px;
  display: flex;
  justify-content: flex-end;
  align-items: center; }
  .header__title {
    font-family: "Roboto", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    margin-right: auto;
    margin-top: 8px; }
  div > .header__search {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    max-width: 200px;
    background-color: #dfdfdf;
    border: none;
    margin: 5px 48px 8px 0;
    border-radius: 8px;
    outline: none;
    padding: 8px 20px;
    transition: background-color 0.2s ease; }
    div > .header__search:hover, div > .header__search:focus {
      background-color: #c8cccf; }
    div > .header__search::-webkit-input-placeholder {
      color: #000000;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px; }
    div > .header__search::-ms-input-placeholder {
      color: #000000;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px; }
    div > .header__search::placeholder {
      color: #000000;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px; }

.search-form {
  margin: 25px 40px 60px;
  border-bottom: solid 1px #000000; }
  div > .search-form__select {
    border-radius: 8px;
    background-color: #dfdfdf;
    transition: background-color 0.2s ease;
    padding-right: 4px; }
    div > .search-form__select:hover, div > .search-form__select:focus, div > .search-form__select:active {
      background-color: #c8cccf; }
  div > .search-form__input--text {
    border-radius: 8px; }
  .search-form__text {
    padding-top: 12px; }
  .search-form__input--margins {
    margin-bottom: 8px; }

.has-squere::before {
  display: inline-block;
  background-color: #494949;
  margin-right: 8px;
  content: "";
  width: 10px;
  height: 10px; }

.strings-list {
  margin: 0 40px 50px; }
  a > .strings-list__button {
    margin-bottom: 24px; }
  .strings-list__elements {
    margin-bottom: 24px; }
  table > .strings-list__heading {
    border-bottom: solid 1px #9e9e9e; }
  .strings-list__body {
    padding-top: 27px; }
  div > .strings-list__table {
    margin-bottom: 0; }
  .strings-list__pagination {
    display: flex;
    justify-content: flex-end;
    margin-top: 25px; }

.table td,
.table th {
  text-align: center; }

.logo {
  padding: 0 10%;
  margin: 0 auto 60px;
  min-width: 150px; }

.sideBarUser {
  margin: 0 auto 50px;
  text-align: center; }
  .sideBarUser__avatar {
    margin: 0 auto 5px; }

.navLinks {
  padding-left: 20%; }
  .navLinks__list {
    display: flex;
    flex-direction: column; }
  .navLinks__item {
    text-decoration: none;
    color: #000000;
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    transition: color 0.2s ease; }
    .navLinks__item:hover {
      color: tomato; }

.user-menu {
  display: flex;
  align-items: center;
  margin-left: 8px;
  padding: 8px 14px;
  border-radius: 8px;
  transition: background-color 0.2s ease;
  margin-bottom: 8px; }
  .user-menu:hover, .user-menu:focus,
  .show > span > .user-menu {
    background-color: #dfdfdf; }
  div > .user-menu__dropdown {
    display: inline-block; }
  .user-menu__name {
    margin-left: 4px; }
  .user-menu__avatar {
    width: 30px;
    height: 30px; }
  .show > span > div > .user-menu__arrow--container {
    -webkit-transform: rotate(0.5turn);
            transform: rotate(0.5turn); }
  .user-menu__arrow--container {
    margin-left: 8px; }

.spinner-container {
  background-color: rgba(0, 0, 0, 0.7);
  top: 30%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  z-index: 1100;
  width: 180px;
  height: 180px;
  pointer-events: none; }

